.contact-section-home {
  padding-top: 110px;
  padding-bottom: 110px;
}
@media screen and (max-width: 500px) {
  .contact-section-home {
    padding-top: 64px;
    padding-bottom: 64px;
  }
}
@media screen and (max-width: 550px) {
  .contact-section-home .txt-content .quote-by-txt {
    padding-left: 4.4vw;
  }
}
@media screen and (min-width: 551px) and (max-width: 1024px) {
  .contact-section-home .txt-content .quote-by-txt {
    padding-left: 22px;
  }
}
@media screen and (min-width: 1025px) {
  .contact-section-home .txt-content .quote-by-txt {
    padding-left: 1.198vw;
  }
}
@media screen and (min-width: 1921px) {
  .contact-section-home .txt-content .quote-by-txt {
    padding-left: 23px;
  }
}
.contact-section-home .txt-content .quote-by-txt::before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 0;
  backface-visibility: hidden;
  transform: translateY(-50%);
  background-color: #002ea6;
  width: 11px;
  height: 2px;
}
.contact-section-home .circle-filler-el {
  stroke: rgba(23, 252, 196, 0.2);
  stroke-width: 0.5px;
}
.contact-section-home .circle-filler-canvas {
  pointer-events: none;
  backface-visibility: hidden;
  z-index: 1;
}
@media screen and (max-width: 1024px) {
  .contact-section-home .circle-filler-canvas {
    display: none;
  }
}
.contact-section-home .circle-filler-canvas.circle-1 {
  top: 0;
  left: 72.5%;
}
.contact-section-home .circle-filler-canvas.circle-2 {
  top: 69%;
  left: 0;
}
.contact-section-home .circle-filler-canvas.circle-3 {
  top: 69%;
  left: 72.2%;
}
.contact-section-home .img-hold {
  overflow: hidden;
}
.contact-section-home .blocks-wrap {
  display: flex;
  flex-flow: row wrap;
  align-items: flex-start;
  justify-content: space-around;
}
.contact-section-home .single-block {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  box-sizing: content-box;
  overflow: hidden;
}
@media screen and (max-width: 1024px) {
  .contact-section-home .single-block {
    flex-flow: column nowrap;
    justify-content: center;
    text-align: center;
  }
}
@media screen and (min-width: 1025px) {
  .contact-section-home .single-block {
    flex-direction: column;
    gap: 10px;
  }
}
.contact-section-home .single-block.twilio-chat-btn:hover {
  cursor: pointer;
}
.contact-section-home .svg-icon-wrap {
  border-radius: 50%;
  background-color: #17fcc4;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
@media screen and (max-width: 767px) {
  .contact-section-home .svg-icon-wrap {
    height: 50px;
    width: 50px;
    margin-bottom: 25px;
  }
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .contact-section-home .svg-icon-wrap {
    margin-bottom: 25px;
  }
}
@media screen and (min-width: 1921px) {
  .contact-section-home .svg-icon-wrap {
    margin-right: 23px;
  }
}
.contact-section-home .svg-icon-wrap.triangle-element::before {
  content: "";
  display: inline-block;
  position: absolute;
  width: 0;
  height: 0;
  border-style: solid;
  border-color: #17fcc4 transparent transparent transparent;
}
@media screen and (max-width: 550px) {
  .contact-section-home .svg-icon-wrap.triangle-element::before {
    bottom: -2.6vw;
    right: 3vw;
    border-width: 17px 16px 0 0;
  }
}
@media screen and (min-width: 551px) and (max-width: 1024px) {
  .contact-section-home .svg-icon-wrap.triangle-element::before {
    bottom: -13px;
    right: 15px;
    border-width: 17px 16px 0 0;
  }
}
@media screen and (min-width: 1025px) {
  .contact-section-home .svg-icon-wrap.triangle-element::before {
    bottom: -0.677vw;
    right: 0.781vw;
    border-width: 17px 16px 0 0;
  }
}
@media screen and (min-width: 1921px) {
  .contact-section-home .svg-icon-wrap.triangle-element::before {
    bottom: -13px;
    right: 15px;
    border-width: 17px 16px 0 0;
  }
