.contact-section-home .contact-icon {
  flex: 0 0 auto;
}
.contact-section-home .contact-icon path,
.contact-section-home .contact-icon circle {
  fill: #fff;
}
.contact-section-home .msg-icon {
  width: 51px;
  height: 51px;
  padding: 5px 10px;
}
@media screen and (max-width: 767px) {
  .contact-section-home .msg-icon {
    width: 45px;
    height: 45px;
  }
}
.contact-section-home .msg-icon .st0 {
  opacity: 1;
}
.contact-section-home .email-btn .cls-1-top {
  transition: transform 0.3s cubic-bezier(0.22, 0.61, 0.36, 1);
}
.contact-section-home .email-btn:hover .cls-1-top {
  transform: translate(0, 52%) rotateX(130deg);
}
.contact-section-home .email-icon {
  width: 51px;
  height: 51px;
}
@media screen and (max-width: 767px) {
  .contact-section-home .email-icon {
    width: 45px;
    height: 45px;
  }
}
email-btn .contact-section-home .email-icon.bottom-email-icon {
  left: 6px;
  bottom: 0;
  transform: none;
  top: 10px;
  transform-origin: unset;
  width: 45px;
  height: 45px;
}
.contact-section-home .email-icon.top-part-svg {
  left: 28px;
  top: 30px;
}
.contact-section-home .phone-icon {
  width: 51px;
  height: 51px;
  padding: 11px 5px 13px 9px;
}
@media screen and (max-width: 767px) {
  .contact-section-home .phone-icon {
    width: 45px;
    height: 45px;
  }
}
.contact-section-home .phone-icon .signal-path {
  opacity: 0;
}
.contact-section-home .msg-icon .st0 {
  fill-rule: evenodd;
  clip-rule: evenodd;
}

